#navbar {
    z-index: 5;
    width: 100%;
    transition: top 0.7s;
    position: fixed;
    --bs-bg-opacity: 0.65;
    background-color: black;
    top: -74px 
}

#navbar > img{
    height: 50px
}

#nav-container{

}


#navbar #logo {
    color: #f2f2f2;
    font-size: 30px;
    padding: 10px 10px;
}

#navbar a {
    float: right;
    display: block;
    color: white;
    text-align: center;
    padding: 15px;
    text-decoration: none;
  }
  
  #navbar a:hover {
    background-color: #ddd;
    color: black;
  }
  
  .line-container {
    position: fixed;
    justify-content: center;
    display: flex;
    top: 50px;
    left: 0;
    width: 100vw;
    text-align: center;
    overflow: hidden;
  }
  
  svg {
    width: 100vw;
    position: fixed;
    margin-left: auto;
    margin-right: auto;
  }
  
  
  .two {
    font-size: 3em;
    position: relative;
    overflow: hidden;
  }
  
  section .ptemp {
    position: absolute;
    z-index: -1;
    color: blueviolet;
    top: 10%;
    left: 20%;
    white-space: nowrap;
    font-size: 4rem;
  }
  
  .three {
    background: url(../assets/winter.jpg);
    background-size: cover;
    overflow: hidden;
  }
  
  .four {
    background: url(../assets/mountain.jpg);
    background-size: cover;
    overflow: hidden;
  }
  
  .one,
  .two,
  .three,
  .four {
    height: 100vh;
    width: 100vw;
  }
  
  #page-container {
    position: relative;
    min-height: 100vh;
  }
  
  .icon {
    color: white;
    font-size: 1.5em;
    padding: 1.5rem 0.4rem 0.4rem 0.4rem;
  }
  
  .icon:hover {
    font-size: 2em;
    padding: 1rem 0.3rem 0.3rem 0.3rem;
  }
  
